.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App {
  text-align: center;
  font-family: Arial, sans-serif;
  background-image: url('./bg.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.todoheading {
  font-family: 'Roboto', sans-serif;
  font-size: 60px; /* Increased font size */
  font-weight: bold;
  color: #ffff00;
  text-shadow: 0 0 3px #ffff00, 0 0 6px #ffff00, 0 0 9px #ffff00;
  margin-bottom: 30px;
  position: absolute; /* Added positioning */
  top: 30%; /* Adjust this value to change the heading's vertical position */
  left: 50%;
  transform: translate(-50%, -50%);
}

.addtask {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

input {
  padding: 5px 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  background-color: rgba(255, 255, 255, 0.8);
  color: #333;
  border-radius: 4px;
  outline: none;
  margin-right: 10px;
}

button {
  padding: 5px 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  background-color: #4caf50;
  border-radius: 4px;
  cursor: pointer;
  outline: none;
  transition: background-color 0.3s;
}

.done {
  margin-left: 10px;
  padding: 4px;
}

button:hover {
  background-color: #45a049;
}

.list h1 {
  display: inline-block;
  font-size: 24px;
  margin: 5px 0;
}

.delete-btn {
  background-color: #f44336;
  margin-left: 10px;
  border-radius: 4px;
  border: none;
  color: white;
  padding: 5px 10px;
  font-size: 16px;
  cursor: pointer;
  outline: none;
  transition: background-color 0.3s;
}

.delete-btn:hover {
  background-color: #da190b;
}
